import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import millify from 'millify';
import { Col, Row, Typography,Flex,Spin } from 'antd';

import { useGetCoinDetalisQuery } from '../services/cryptoApi';

const { Title } = Typography;

const CryptoDetail = () => {
  const { coinId } = useParams();
  const { data, isFetching } = useGetCoinDetalisQuery(coinId);
  const cryptoCoinDetails = data?.data?.coin;
if(isFetching)  {
  return(
    <Flex align="center" gap="middle">
    <Spin size="large" />
  </Flex>
  )
}

    if (cryptoCoinDetails) {
      return(
        <Col className="coin-detail-container">
        <Col className="coin-heading-container">
          <Title level={2} className="coin-name" style={{color: `${cryptoCoinDetails?.color}`}}>
            {cryptoCoinDetails.name} Price
          </Title>
          <p> {cryptoCoinDetails.name} live price in US Dollar (USD). View value statistics, market cap and supply.</p>
        </Col>
        <Row className="coin-total">
        <h3>Its Price : {millify(cryptoCoinDetails?.price)}</h3>
        <h3>Its Change : {millify(cryptoCoinDetails?.change)}</h3>
        </Row>
        <Col className="coin-desc-link">
          <Row className="coin-desc">
            <Title level={3} className="coin-details-heading" style={{color: `${cryptoCoinDetails?.color}`}} >What is {cryptoCoinDetails.name}?</Title>
            {cryptoCoinDetails.description}
          </Row>
          <Col className="coin-links">
            <Title level={3} className="coin-details-heading" style={{color: `${cryptoCoinDetails?.color}`}}>{cryptoCoinDetails.name} Links</Title>
            {cryptoCoinDetails.links?.map((link) => (
              <Row className="coin-link" key={link.name}>
                <Title level={5} className="link-name">{link.type}</Title>
                <a href={link.url} target="_blank" rel="noreferrer" style={{color: `${cryptoCoinDetails?.color}`}}>{link.name}</a>
              </Row>
            ))}
          </Col>
        </Col>
      </Col>
      )
    }
}

export default CryptoDetail