import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
const BASE_URL = 'https://coinranking1.p.rapidapi.com/';
const BASE_Parms =  {
    referenceCurrencyUuid: 'yhjMzLPhuIDl',
    timePeriod: '7d',
    'tiers[0]': '1',
    orderBy: 'marketCap',
    orderDirection: 'desc',
    limit: '20',
    offset: '0'
};
const BASE_Headers =  {
    'X-RapidAPI-Key': 'db5e280e1emsh134df3e382e840fp12be1ejsndfa2734b829c',
    'X-RapidAPI-Host': 'coinranking1.p.rapidapi.com'
};
export const cryptoApi = createApi({
    reducerPath: 'cryptoApi',
    baseQuery: fetchBaseQuery({ baseUrl:BASE_URL }),
    endpoints: (builder) => ({
        getCoins: builder.query({
            query: () => ({
                url: 'coins',
                params:BASE_Parms,
                headers:BASE_Headers
            }),
        }),
        getCoinDetalis: builder.query({
            query: (coinId) => ({
                url: `coin/${coinId}`,
                params:BASE_Parms,
                headers:BASE_Headers
            }),
        }),
        getCoinHistory: builder.query({
            query: (coinId) => ({
                url: `coin/${coinId}/history`,
                params:BASE_Parms,
                headers:BASE_Headers
            }),
        }),
    }),
});
export const { useGetCoinsQuery,useGetCoinDetalisQuery,useGetCoinHistoryQuery } = cryptoApi;

