import React from 'react';
import { useGetNewsDataQuery } from '../services/NewsApi';
import { Card, Col, Row, Badge, Typography,Flex,Spin } from 'antd';
import { Link } from 'react-router-dom';
const { Meta } = Card;
const { Title } = Typography;

const News = () =>
{
  const { data,isFetching} = useGetNewsDataQuery();
  const newsData = data?.news;
  if(isFetching)  {
    return(
      <Flex align="center" gap="middle">
      <Spin size="large" />
    </Flex>
    )
  }
  let mapData;
  if (newsData)
  {

    mapData = newsData.map((news, index) =>
    {
if (news.Image) {
  return (
    <Col key={ news.id } xs={ 22 } sm={ 12 } lg={ 4 } className='crypto-card'>
    <Link to={news.Url}>
        <Badge.Ribbon text={ '#' + index } color="#001529">
          <Card hoverable bordered
            cover={ <img alt="newsImg" src={ news.Image } /> }
          >

            <Meta
              className='card-title'
            />

            <h3 >{ news.Title } </h3>
            <h6> Source : { news.Source } </h6>
          </Card>
        </Badge.Ribbon>
        </Link>
    </Col>
  );
}
        
    });
  }

  return (
    <>
      <Title level={ 2 } className="heading">Latest Crypto News</Title>
      <Row className='crypto-card-container' justify='center'>
{mapData}

      </Row>
    </>
  );
};

export default News;