import React from 'react';
import { Avatar, Card, Col, Row, Badge,Flex,Spin } from 'antd';
import millify from "millify";
import { useGetCoinsQuery } from '../services/cryptoApi';
import { Link } from 'react-router-dom';
const { Meta } = Card;
const Cards = () =>
{
  const { data,isFetching } = useGetCoinsQuery();
  const coinsData = data?.data?.coins;
  let mapData;
  if(isFetching)  {
    return(
      <Flex align="center" gap="middle">
      <Spin size="large" />
    </Flex>
    )
  }
  if (coinsData)
  {
    mapData = coinsData.map(coin =>
    {
      return (
        <Col key={ coin.rank } xs={ 22 } sm={ 12 } lg={ 4 } className='crypto-card coin-card'>
          <Link to={ `/coins/${coin.uuid}` }>
            <Badge.Ribbon text={ '#' + coin.rank } color="#001529">
              <Card hoverable bordered>
                <Meta
                  title={ coin.name }
                  avatar={ <Avatar src={ coin.iconUrl } /> }
                  className='card-title'
                />
                <p>Price :{ millify(coin.price) } $</p>
                <p>Market Cap :{ millify(coin.marketCap) }</p>
                <p>Daily Change :{ millify(coin.change) } %</p>
              </Card>
            </Badge.Ribbon>
          </Link>
        </Col>
      );
    });
  }


  return (

    <>
      <Row className='crypto-card-container' justify='center'>
        { mapData }
      </Row>
    </>

  );
};

export default Cards;
