// import Avatar from 'antd/es/avatar/avatar';
import { Avatar, Menu, Typography } from 'antd';
import Icon from '../Images/cryptocurrency.png';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import {  HomeOutlined, FundOutlined, MoneyCollectOutlined, BuildOutlined,AlignRightOutlined } from '@ant-design/icons';
const { Item }=Menu
const NavBar = () =>
{
  const [showMenu,setShowMenu ]=useState(false)
  
  return (
    <div className='navbar'>
      <div className='nav-container'>
        <div className='logo-container'>
          <Avatar src={ Icon } size='large' />
          <Typography.Title level={ 3 } className='logo' >
            <Link to='/'>CRPTOAPP</Link>
          </Typography.Title>
        </div>
        <AlignRightOutlined className='bar' onClick={()=>setShowMenu(!showMenu)}/>
        <Menu theme="dark" className={!showMenu ? 'ant-menu' :'show-menu'}>
          <Item icon={ <HomeOutlined /> }>
            <Link to='/'>Home</Link>
          </Item>
          <Item icon={ <FundOutlined /> }>
            <Link to='/cryptocurrencies'>Cryptocurrencies</Link>
          </Item>
          <Item icon={ <BuildOutlined /> }>
            <Link to='/news'>News</Link>
          </Item>
        </Menu>
      </div>
      
    </div>
  );
};
export default NavBar;