import { Col, Row, Statistic, Typography,Flex,Spin, Space } from "antd";
import { useGetCoinsQuery } from "../services/cryptoApi";
import millify from "millify";
import Cryptocurrencies from "./Cryptocurrencies";
import News from './News';
const { Title } = Typography;

const Home = () =>
{
  const { data, isFetching } = useGetCoinsQuery();
  const golbalData = data?.data?.stats;
  if(isFetching)  {
    return(
      <Flex align="center" gap="middle">
      <Spin size="large" />
    </Flex>
    )
  }

  return (
    <>
      <section className="main-section">
      <Title level={2} className="heading">Global Crypto Stats</Title>
        <Row>
          <Col span={ 12 }><Statistic title='Total Exchanges' value={ golbalData?.total } /></Col>
          <Col span={ 12 }><Statistic title='Total Exchanges' value={ golbalData?.totalExchanges } /></Col>
          <Col span={ 12 }><Statistic title='Total Markets' value={ millify(golbalData?.totalMarkets) } /></Col>
          <Col span={ 12 }><Statistic title='Total MarketCap' value={ millify(golbalData?.totalMarketCap) } /></Col>
          <Col span={ 12 }><Statistic title='Total 24h Volume' value={ millify(golbalData?.total24hVolume) } /></Col>
        </Row>
        </section>
      <Cryptocurrencies />
      <Space>
      <News />
      </Space>
    </>
    
  );
};

export default Home;